import styled from "styled-components";

export const TITLECTS = [
  {
    tit1: "금융시장 투자 온도계",
  },
  {
    tit1: "금융시장 투자온도계",
    tit2: "결과 분석",
  },
];

function Title({ idx, workdttm }) {
  const dateToString = (date) => {
    let str = date + "";

    let year = str.slice(0, 4);
    let month = str.slice(4, 6);
    let day = str.slice(6, 8);

    return date == undefined ? null : year + "." + month + "." + day;
  };

  return (
    <div className={`tit tit_${idx}`}>
      {idx == 0 ? (
        <>
          <h2>
            <span className="titSpan">SMART Index</span>
            {TITLECTS[idx].tit1}
          </h2>

          <div className="flex">
            <div>
              <p>
                콴텍의 AI알고리즘이 글로벌금융시장의 변동성과 위험수준을​ 매일의
                온도로 알려드립니다.
              </p>
              <span className="titSpan spSmall">
                기준일 : {dateToString(workdttm)}
              </span>
            </div>
          </div>
        </>
      ) : (
        <>
          <h2>
            <span className="titSpan">SMART Index</span>
            {TITLECTS[idx].tit1}
            <br />
            {TITLECTS[idx].tit2}
          </h2>
        </>
      )}
    </div>
  );
}

export default Title;

const DownBtn = styled.div``;
