import { popupExSvg, closeBtnSvg, logoSvg } from "./../img/SvgStore";

// 팝업 내용
export const POPUPCTS = [
  {
    mntit: "투자온도 및 구간별 설명",
    tit1: "투자온도란?",
    con1: "투자온도는 투자자산 비중의 한도 비율입니다. 예를 들어, 85°c로 투자 온도가 나왔다면, 투자자산의 비중을 85% 한도 내에서 편입하고, 나머지를 안전자산으로 편입하시는 것을 권해드리는 것입니다.",
    con2: "온도가 상승 추세일 때는 한도에 가깝게, 하락 추세일 때는 한도보다 적게 투자자산을 가져가는 것이 바람직합니다.",
    tit2: "구간별 설명",
    img: popupExSvg(),
    close: closeBtnSvg(),
  },
  {
    mntit: "콴텍 자세히 알아보기",
    con1: "오직 숫자로 증명하는 AI 핀테크 업체, 콴텍! 콴텍은 빅데이터와 인공지능을 활용한​ 디지털 자산관리 로보어드바이저입니다.​",
    con2: "매일 국내/해외 시장 데이터를 면밀히 분석하여 하루의 투자 온도를 고객님께 알려드립니다.​",
    con3: "콴텍은 고객님 자산의 든든한 파수꾼으로서의 역할에 최선을 다하겠습니다.​",
    img: logoSvg(),
  },
];

// 팝업 열렸을 때
export const POPUP = ({ closePopup, poptxt }) => {
  const Contxt = (poptxt) => {
    const con = POPUPCTS[poptxt];

    switch (poptxt) {
      case 0:
        return (
          <div className="fulllPopup">
            <div className="flex">
              <div
                onClick={() => {
                  closePopup();
                }}
              >
                {closeBtnSvg()}
              </div>
              <h2>{con.mntit}</h2>
            </div>

            <div className="fullPop">
              <button
                onClick={() => {
                  closePopup();
                }}
              >
                확인
              </button>
            </div>

            <div className="popcts">
              <div>
                <div>
                  <h4>{con.tit1}</h4>
                  <p>{con.con1}</p>
                  <br />
                  <p>{con.con2}</p>
                </div>
                <div className="contour" />
                <div>
                  <h4>{con.tit2}</h4>
                  <p className="img">{con.img}</p>
                </div>
              </div>
            </div>
          </div>
        );
      case 1:
        return (
          <div className="layerPopup">
            <div className="layerWrap">
              <div className="flex">
                <div
                  onClick={() => {
                    closePopup();
                  }}
                >
                  {closeBtnSvg()}
                </div>
                <h2>{con.mntit}</h2>
              </div>

              <div className="popcts">
                <p>{con.con1}</p>
                <br />
                <p>{con.con2}</p>
                <br />
                <p>{con.con3}</p>
                <br />
                <p className="logo">{con.img}</p>
              </div>
            </div>
          </div>
        );
    }
  };

  return (
    <div className="popup">
      <div>{Contxt(poptxt)}</div>
    </div>
  );
};
