import React from "react";
import { logoSvg } from "./../img/SvgStore";

const Notice = () => {
  return (
    <div className="notice">
      <h4>* 유의사항</h4>
      <ul>
        <li>
          투자 온도계 의견은 하나은행과 QUANTEC과의 업무제휴로 QUANTEC에서
          제공되는 서비스이며, 제휴 계약 종료 시 서비스는 중단될 수 있습니다.
        </li>
        <li>
          본 자료는 금융시장의 이해를 돕기 위한 참고 자료로써 손님의 투자결과에
          대한 책임 및 법적 책임소재의 증빙자료로 사용될 수 없습니다.
          <p className="logo">{logoSvg()}</p>
        </li>
      </ul>
    </div>
  );
};

export default Notice;
